import { useState } from 'react';
import { useLocation, Link } from 'react-router-dom';
import { motion, AnimatePresence } from 'framer-motion';
import { Bars3Icon, XMarkIcon } from '@heroicons/react/24/outline';
import NavigationDropdown from './NavigationDropdown';

const Navigation = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [openDropdown, setOpenDropdown] = useState<string | null>(null);
  const location = useLocation();

  const simpleMenuItems = [
    { name: '', path: '/' },
    { name: '', path: '/gallery' },
    { name: '', path: '/videos' },
    { name: '', path: '/press' },
  ];

  const campItems = [
    { name: '2023 ', path: '/camps/2023' },
    { name: '2025 ', path: '/camps/2025' },
    { name: '2026 ', path: '/camps/2026' },
    { name: ' ', path: '/camps' },
  ];

  const albumItems = [
    { name: ' ', path: '/album/about' },
    { name: '', path: '/album/musicians' },
    { name: '', path: '/album/tracks' },
  ];

  return (
    <nav className="fixed w-full bg-cloud-white/90 backdrop-blur-md z-50 shadow-sm">
      <div className="container mx-auto px-4">
        <div className="flex justify-between items-center h-16">
          <Link to="/" className="text-2xl font-bold font-serif text-jeju-ocean hover:text-ocean-mist transition-colors">
            
          </Link>

          {/* Desktop Menu */}
          <div className="hidden md:flex items-center space-x-8">
            {simpleMenuItems.map((item) => (
              <Link
                key={item.path}
                to={item.path}
                className={`${location.pathname === item.path
                    ? 'text-jeju-ocean font-bold'
                    : 'text-deep-ocean hover:text-jeju-ocean'
                  } transition-colors duration-300 font-serif`}
              >
                {item.name}
                {location.pathname === item.path && (
                  <motion.div
                    className="absolute bottom-0 left-0 w-full h-0.5 bg-golden-sun"
                    layoutId="underline"
                  />
                )}
              </Link>
            ))}
            <NavigationDropdown
              label=""
              items={campItems}
              isOpen={openDropdown === 'camps'}
              onOpenChange={(isOpen) => setOpenDropdown(isOpen ? 'camps' : null)}
            />
            <NavigationDropdown
              label=""
              items={albumItems}
              isOpen={openDropdown === 'album'}
              onOpenChange={(isOpen) => setOpenDropdown(isOpen ? 'album' : null)}
            />
          </div>

          {/* Mobile Menu Button */}
          <div className="md:hidden">
            <button
              onClick={() => setIsOpen(!isOpen)}
              className="text-deep-ocean hover:text-jeju-ocean transition-colors"
            >
              {isOpen ? (
                <XMarkIcon className="h-6 w-6" />
              ) : (
                <Bars3Icon className="h-6 w-6" />
              )}
            </button>
          </div>
        </div>
      </div>

      {/* Mobile Menu */}
      <div className="mobile-menu-wrapper">
        <AnimatePresence initial={false}>
          {isOpen && (
            <motion.div
              initial={{ opacity: 0, height: 0 }}
              animate={{ opacity: 1, height: "auto" }}
              exit={{ opacity: 0, height: 0 }}
              className="md:hidden bg-cloud-white/95 backdrop-blur-md border-t border-seafoam"
            >
              <div className="container mx-auto px-4 py-4">
                {simpleMenuItems.map((item) => (
                  <Link
                    key={item.path}
                    to={item.path}
                    className={`block py-2 ${location.pathname === item.path
                        ? 'text-jeju-ocean font-bold'
                        : 'text-deep-ocean'
                      } font-serif`}
                    onClick={() => setIsOpen(false)}
                  >
                    {item.name}
                  </Link>
                ))}

                {/* Mobile Dropdowns */}
                <div className="border-t border-sage-gray my-4 pt-4">
                  <button
                    onClick={() => setOpenDropdown(openDropdown === 'camps' ? null : 'camps')}
                    className="w-full text-left py-2 font-serif text-text-dark flex justify-between items-center"
                  >
                    
                    <motion.span
                      animate={{ rotate: openDropdown === 'camps' ? 180 : 0 }}
                      transition={{ duration: 0.2 }}
                    >
                      
                    </motion.span>
                  </button>
                  <AnimatePresence>
                    {openDropdown === 'camps' && (
                      <motion.div
                        initial={{ opacity: 0, height: 0 }}
                        animate={{ opacity: 1, height: 'auto' }}
                        exit={{ opacity: 0, height: 0 }}
                        className="pl-4 overflow-hidden"
                      >
                        {campItems.map((item) => (
                          <Link
                            key={item.path}
                            to={item.path}
                            className="block py-2 text-text-dark hover:text-deep-sage font-serif text-sm"
                            onClick={() => {
                              setIsOpen(false);
                              setOpenDropdown(null);
                            }}
                          >
                            {item.name}
                          </Link>
                        ))}
                      </motion.div>
                    )}
                  </AnimatePresence>

                  <button
                    onClick={() => setOpenDropdown(openDropdown === 'album' ? null : 'album')}
                    className="w-full text-left py-2 font-serif text-text-dark flex justify-between items-center"
                  >
                    
                    <motion.span
                      animate={{ rotate: openDropdown === 'album' ? 180 : 0 }}
                      transition={{ duration: 0.2 }}
                    >
                      
                    </motion.span>
                  </button>
                  <AnimatePresence>
                    {openDropdown === 'album' && (
                      <motion.div
                        initial={{ opacity: 0, height: 0 }}
                        animate={{ opacity: 1, height: 'auto' }}
                        exit={{ opacity: 0, height: 0 }}
                        className="pl-4 overflow-hidden"
                      >
                        {albumItems.map((item) => (
                          <Link
                            key={item.path}
                            to={item.path}
                            className="block py-2 text-text-dark hover:text-deep-sage font-serif text-sm"
                            onClick={() => {
                              setIsOpen(false);
                              setOpenDropdown(null);
                            }}
                          >
                            {item.name}
                          </Link>
                        ))}
                      </motion.div>
                    )}
                  </AnimatePresence>
                </div>
              </div>
            </motion.div>
          )}
        </AnimatePresence>
      </div>
    </nav>
  );
};

export default Navigation;
