import { GalleryImage } from '../types/gallery';

export const getGalleryImages = async (): Promise<GalleryImage[]> => {
  //     (1 180)
  const imageCount = 180;
  const images: GalleryImage[] = [];
  
  //   (180 1)
  for (let i = imageCount; i >= 1; i--) {
    images.push({
      id: i,
      url: `/images-webp/gallery/${i}.webp`
    });
  }
  
  return images;
};
